import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";
import appNoGoods from '../../../components/page-component/app-no-goods/app-no-goods.vue';

import {mapGetters, mapState} from "vuex";

export default {
    data() {
        return {
            tabList: [
                {id:1, name: '未使用'},
                {id:2, name: '已使用'},
                {id:3, name: '已过期'}
            ],
            show: false,
            img: '/images/share/img-share-order.png',
            activeTab: 1,
            list: [],
            more: false,
            page: 2,
            title: '暂无未使用卡券~'
        }
    },
    components: {
        'app-tab-nav': appTabNav,
        'app-no-goods': appNoGoods,
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
        }),
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        })
    },
    methods: {
        saveImg() {
            this.$utils.batchSave(this.img).then(res => {
                uni.showToast({title: '保存成功', icon: 'none'});
            })
        },

        showModal(id) {
            let that = this;
            if(id > 0) {
                uni.showLoading({
                    text: '生成中...'
                });
                that.$request({
                    url: that.$api.card.qrcode,
                    data: {
                        cardId: id,
                    },
                    method: 'get'
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        that.show = true;
                        that.img = response.data.file_path;
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000,
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    uni.showToast({
                        title: response,
                        icon: 'none',
                        duration: 1000,
                    });
                });
            }
        },

        tabStatus(e) {
            this.list = [];
            let id = e.currentTarget.dataset.id;
            if (id === 1) {
                this.title = `暂无未使用优惠券~`;
            } else if (id === 2) {
                this.title = `暂无已使用优惠券~`;
            } else if (id === 3) {
                this.title = `暂无已过期优惠券~`;
            }
            this.page = 2;
            this.activeTab = id;
            this.getList();
        },
        getList() {
            let that = this;
            that.$showLoading({
                text: '加载中...'
            });
            that.$request({
                url: that.$api.card.index,
                data: {
                    status: that.activeTab
                },
            }).then(response=>{
                that.$hideLoading();
                if(response.code === 0) {
                    that.list = response.data.list;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.$hideLoading();
                that.$event.on(that.$const.EVENT_USER_LOGIN).then(()=>{
                    that.getList();
                });
            });
        },
        getMore() {
            let that = this;
            uni.showLoading({
                title: '加载中...',
            });
            that.$request({
                url: that.$api.card.index,
                data: {
                    page: that.page,
                    status: that.activeTab
                },
            }).then(response=>{
                uni.hideLoading();
                if(response.code === 0) {
                    if(response.data.list.length > 0) {
                        that.loading = null;
                        that.list = that.list.concat(response.data.list);
                        that.page++;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000,
                    });
                }
            }).catch(() => {
                that.$hideLoading();
            });
        },
        toDetail(card) {
            if (card.is_allow_send == 1) {
                uni.navigateTo({
                    url: `/pages/card/details/details?id=${card.id}`
                });
            } else {
                uni.navigateTo({
                    url: `/pages/card/details/details-no-share?id=${card.id}`
                });
            }
        },
    },
    onLoad() { this.$commonLoad.onload();
        this.getList();
    },
    onReachBottom() {
        this.getMore();
    }
}
